<template>
  <div class="customer-profile">
    <vue-user-info
      :user-info="getCustomerInfo"
      :user-role="userStore.userRoleInfo"
      v-if="props.tabId === 1"
    />

    <div class="order-active__cards" v-if="props.tabId === 2">
      <vue-customer-active-card
        v-for="(card, index) of getCustomerInfo.activeOrder"
        :key="index"
        :card="card"
        v-if="getLengthActiveCards > 0"
        v-loading="loadingActiveCard"
      />
      <div class="order-active__warning" v-else-if="getLengthActiveCards === 0">
        <p class="order-active__warning--p">Активных заказов нет</p>
      </div>
    </div>

    <div class="order-done__cards" v-if="props.tabId === 3">
      <vue-customer-done-card
        v-for="(card, index) of getCustomerInfo.doneOrder"
        :key="index"
        :card="card"
        v-if="getLengthDoneCards > 0"
        v-loading="loadingDoneCard"
      />
      <div class="order-done__warning" v-else-if="getLengthDoneCards === 0">
        <p class="order-done__warning--p">Завершенных заказов нет</p>
      </div>
    </div>

    <div class="user-settings" v-if="props.tabId === 4">
      <vue-customer-settings />
    </div>

    <div class="offer-profile" v-if="props.tabId === 5">
      <vue-offer-info-block />
    </div>

    <div class="statistics-block" v-if="props.tabId === 6">
      <vue-customer-statistics :user-info="userInfo" />
    </div>

    <div class="create-order" v-if="props.tabId === 7">
      <div class="create-order__title shadow-block h3">
        <h3>Создание заказа</h3>
        <img src="../../assets/icon-for-menu-profile/create-order.svg" alt="" />
      </div>
      <vue-create-order-form />
    </div>

    <div class="customer-template">
      <vue-customer-template v-if="props.tabId === 8" />
    </div>
  </div>
</template>
<script lang="ts" setup>
import VueUserInfo from "../Forms/VueUserInfo.vue";
import customerInfo from "@/localDatabase/customerInfo.json";
import { computed, defineProps, ref, watch } from "vue";
import CustomerInfo from "@/interfaces/CustomerInfo";
import VueCustomerActiveCard from "../Cards/VueCustomerActiveCard.vue";
import VueCustomerDoneCard from "../Cards/VueCustomerDoneCard.vue";
import VueCreateOrderForm from "../Forms/VueCreateOrderForm.vue";
import VueCustomerTemplate from "../Template/VueCustomerTemplate.vue";
import VueOfferInfoBlock from "../Offer/VueOfferInfoBlock.vue";
import VueCustomerSettings from "../Settings/VueCustomerSettings.vue";
import VueCustomerStatistics from "../Statistics/VueCustomerStatistics.vue";
import { useUserStore } from "@/store/user";

const props = defineProps({
  tabId: Number,
});

const userStore = useUserStore();
const userInfo = userStore.userInfo;

let loadingDoneCard = ref(true);
let loadingActiveCard = ref(true);

//TODO(tolstov): будем смотреть на получение данных, может и не понадобится!
setTimeout(() => {
  loadingDoneCard.value = false;
}, 1000);
setTimeout(() => {
  loadingActiveCard.value = false;
}, 1000);

const getCustomerInfo = computed((): CustomerInfo => {
  return customerInfo;
});

const getLengthDoneCards = computed((): number => {
  if (getCustomerInfo) {
    return getCustomerInfo.value!.doneOrder.length;
  } else return 0;
});

const getLengthActiveCards = computed((): number => {
  if (getCustomerInfo) {
    return getCustomerInfo.value!.activeOrder.length;
  } else return 0;
});
</script>
<style lang="scss">
.customer-profile {
  font-family: "sf-pro-400";
  font-size: 20px;
  width: 100%;
}
.create-order {
  &__title {
    max-width: 400px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    & img {
      width: 30px;
    }
  }
}
</style>
