<template>
  <div
    class="profile-menu"
    v-staggered-from="{
      delay: 0.05,
      selector: '.performer-menu__item',
      yOffset: 20,
    }"
  >
    <div
      class="performer-menu__item"
      v-for="item of userMenu"
      :key="item.id"
      :class="{ profile_menu__active: item.active }"
      @click="setActive(item.id)"
    >
      <div class="profile-menu__image">
        <img :src="getImageByPathImage(item.img)" alt="" />
        <p>|</p>
      </div>

      <p>{{ item.title }}</p>
    </div>
    <div
      class="active-highlight"
      :style="{
        transform: `translateY(${activeHighlightTop}px)`,
      }"
    ></div>

    <vue-modal
      :is-visible="isVisibleConfirmModal"
      @close-modal="isVisibleConfirmModal = false"
    >
      <div class="confirm-exit">
        <p>Вы действительно хотите выйти из аккаунта?</p>
        <div class="confirm-btns">
          <vue-small-button
            :title="'Отменить'"
            :color="'black'"
            @click="isVisibleConfirmModal = false"
          />
          <vue-small-button :title="'Выйти'" :color="'red'" @click="onExit" />
        </div>
      </div>
    </vue-modal>
  </div>
</template>

<script setup lang="ts">
import {
  ref,
  defineProps,
  onMounted,
  nextTick,
  defineEmits,
  onUnmounted,
  watch,
} from "vue";
import { useUserStore } from "@/store/user";
import { useRouter } from "vue-router";
import { useSettingsStore } from "@/store/settings";
import VueModal from "../Modals/VueModal.vue";
import VueSmallButton from "../Buttons/VueSmallButton.vue";

const store = useUserStore();
const settingStore = useSettingsStore();
const router = useRouter();
const emit = defineEmits(["select"]);

const isVisibleConfirmModal = ref(false);

const props = defineProps({
  user: {
    type: Number,
  },
});

const baseMenuItems = [
  {
    id: 1,
    title: "Личная информация",
    active: true,
    img: "lk",
  },
  {
    id: 2,
    title: "Активные заявки",
    active: false,
    img: "active-card",
  },
  {
    id: 3,
    title: "Завершенные заявки",
    active: false,
    img: "no-active-card",
  },
  {
    id: 4,
    title: "Настройки",
    active: false,
    img: "settings",
  },
  {
    id: 5,
    title: "Оферта",
    active: false,
    img: "offer",
  },
  {
    id: 6,
    title: "Статистика",
    active: false,
    img: "statistics",
  },
];

const exitMenuItem = {
  id: 10,
  title: "Выйти",
  active: false,
  img: "exit",
};

const customerMenu = ref([
  ...baseMenuItems,
  {
    id: 7,
    title: "Создать заказ",
    active: false,
    img: "create-order",
  },
  {
    id: 8,
    title: "Создать шаблон",
    active: false,
    img: "create-template",
  },
  exitMenuItem,
]);

const performerMenu = ref([...baseMenuItems, exitMenuItem]);
const userMenu = props.user === 0 ? customerMenu : performerMenu;

const activeHighlightTop = ref(0);

const getImageByPathImage = (path: string) => {
  return require(`../../assets/icon-for-menu-profile/${path}.svg`);
};

const onExit = () => {
  router.push("/");
  setTimeout(() => {
    store.logOutOfProfile();
  }, 0);
};

const setActive = (id: number) => {
  if (id === 10) {
    isVisibleConfirmModal.value = true;
    return;
  }
  userMenu.value.forEach((item) => {
    item.active = item.id === id;
  });

  updateHighlightPosition();

  emit("select", id);
};

const updateHighlightPosition = () => {
  nextTick(() => {
    const activeItem = document.querySelector(
      ".performer-menu__item.profile_menu__active"
    ) as HTMLElement;
    if (activeItem) {
      activeHighlightTop.value = activeItem.offsetTop;
    }
  });
};

watch(
  () => settingStore.tabsLK,
  (newTabsLK) => {
    newTabsLK.forEach((tab) => {
      if (!tab.isActive) {
        customerMenu.value = customerMenu.value.filter(
          (item) => item.id !== tab.id
        );
      } else {
        const itemExists = customerMenu.value.some(
          (item) => item.id === tab.id
        );
        if (!itemExists) {
          const itemToAdd = [
            ...baseMenuItems,
            {
              id: 7,
              title: "Создать заказ",
              active: false,
              img: "create-order",
            },
            {
              id: 8,
              title: "Создать шаблон",
              active: false,
              img: "create-template",
            },
          ].find((item) => item.id === tab.id);
          if (itemToAdd) {
            customerMenu.value.push(itemToAdd);
            customerMenu.value.sort((a, b) => a.id - b.id);
          }
        }
      }
    });
  },
  { deep: true, immediate: true }
);

watch(
  () => settingStore.tabsLK,
  (newTabsLK) => {
    newTabsLK.forEach((tab) => {
      if (!tab.isActive) {
        performerMenu.value = performerMenu.value.filter(
          (item) => item.id !== tab.id
        );
      } else {
        const itemExists = performerMenu.value.some(
          (item) => item.id === tab.id
        );
        if (!itemExists) {
          const itemToAdd = [...baseMenuItems].find(
            (item) => item.id === tab.id
          );
          if (itemToAdd) {
            performerMenu.value.push(itemToAdd);
            performerMenu.value.sort((a, b) => a.id - b.id);
          }
        }
      }
    });
  },
  { deep: true, immediate: true }
);

onMounted(() => {
  updateHighlightPosition();
  window.addEventListener("resize", updateHighlightPosition);
});

onUnmounted(() => {
  window.removeEventListener("resize", updateHighlightPosition);
});
</script>

<style lang="scss">
@import url(../../styles/variable.css);

.confirm {
  &-btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20%;
  }
  &-exit {
    width: 100%;
    max-width: 400px;
    & p {
      font-size: 20px;
      margin-bottom: 20px;
    }
  }
}

.profile-menu {
  font-family: "sf-pro-400";
  width: 55%;
  display: flex;
  flex-direction: column;
  align-items: end;
  gap: 30px;
  position: relative;
  border-right: 2px solid var(--yellow);
  height: calc(100vh - 250px);

  @media (max-width: 860px) {
    height: 100%;
    justify-content: start;
    align-items: start;
    width: 100%;
    gap: 20px;
  }

  &__image {
    display: flex;
    justify-content: center;
    align-items: center;
    & img {
      margin-right: 5px;
      background: white;
      padding: 5px;
      border-radius: 50%;
    }
  }

  .performer-menu__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 280px;
    font-size: 20px;
    letter-spacing: -1px;
    text-align: end;
    padding: 7px 10px;
    position: relative;
    z-index: 1;
    cursor: pointer;
    @media (max-width: 860px) {
      width: 100%;
      max-width: 100%;
    }

    &.profile_menu__active {
      color: var(--light-black);
    }
  }

  &__exit {
    max-width: 98%;
    width: 100%;
    margin-right: 5px;
    display: flex;
    justify-content: end;
    margin-bottom: 10px;
  }

  .active-highlight {
    position: absolute;
    right: 0; /* Прижимаем к правому краю */
    width: 100%;
    background-color: var(--yellow);
    border-radius: 15px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    transition: transform 0.3s ease, height 0.3s ease;
    z-index: 0;
    max-width: 280px;
    height: 47px;
    @media (max-width: 860px) {
      max-width: 100%;
    }
  }
}
</style>
