<template>
  <div class="profile-page" v-if="getUserRole">
    <div class="profile-page__title">
      <h1>Личный кабинет</h1>
    </div>
    <div class="profile-page__burger" v-if="isMobile">
      <vue-burger v-model="isOpenMenu" />
    </div>
    <div class="profile-page__user">
      <div
        class="profile-performer__content"
        v-if="user === 1 && (!isMobile || (isMobile && !isOpenMenu))"
      >
        <vue-performer-profile
          :tab-id="tabID"
          class="component-performer-profile"
        />
      </div>
      <div
        class="profile-page__customer"
        v-if="user === 0 && (!isMobile || (isMobile && !isOpenMenu))"
      >
        <div class="profile-customer__content">
          <vue-customer-profile
            :tab-id="tabID"
            class="component-customer-profile"
          />
        </div>
      </div>
      <vue-profile-menu
        v-if="!isMobile || (isMobile && isOpenMenu)"
        :user="user"
        @select="onSwitchTab"
      />
    </div>
  </div>
</template>

<script setup lang="ts">
import VueProfileMenu from "@/components/Navigation/VueProfileMenu.vue";
import VuePerformerProfile from "@/components/Profile/VuePerformerProfile.vue";
import VueCustomerProfile from "@/components/Profile/VueCustomerProfile.vue";
import VueBurger from "@/components/Switchers/VueBurger.vue";
import { computed, inject, ref, onMounted, onUnmounted, watch } from "vue";
import { useUserStore } from "@/store/user";

const userStore = useUserStore();

const isOpenMenu = ref<boolean>(false);
const tabID = ref(1);
const user = inject<number>("userRole");
const isMobile = ref(false);

const testUserRole = ref(user === 0 ? "Заказчик" : "Испонитель");
const testUserSwitcher = ref(Boolean(userStore.userRoleInfo.userRole));

watch(
  () => testUserSwitcher.value,
  (newValue) => {
    if (newValue) {
      userStore.initUser(1);
      testUserRole.value = "Исполнитель";
    } else if (!newValue) {
      userStore.initUser(0);
      testUserRole.value = "Заказчик";
    }
  }
);

const getUserRole = computed(() => {
  return user === 1 || user === 0;
});

const checkScreenWidth = () => {
  isMobile.value = window.matchMedia("(max-width: 860px)").matches;
};

onMounted(() => {
  checkScreenWidth();
  window.addEventListener("resize", checkScreenWidth);
});

onUnmounted(() => {
  window.removeEventListener("resize", checkScreenWidth);
});

const onSwitchTab = (id: number) => {
  tabID.value = id;
  isOpenMenu.value = false;
};
</script>

