<template>
  <vue-info-box>Шаблоны находятся на стадии разработки!</vue-info-box>
  <!-- TODO(tolstov): отключены шаблоны на время разработки -->
  <div class="saved-templates" v-if="false">
    <div class="templates" v-if="TEMPLATES.length > 0">
      <vue-template-order
        v-for="template of TEMPLATES"
        :template="template"
        :key="template.id"
      />
    </div>
    <div
      class="not-templates"
      v-if="TEMPLATES.length === 0 && !createdTemplate"
    >
      <div class="not-templates__warning">
        <p>У вас нет созданных или сохраненных шаблонов</p>
      </div>
    </div>
    <div class="created__template">
      <vue-create-template
        v-if="createdTemplate"
        @saved-template="createdTemplate = false"
      />
      <vue-yellow-button
        :title="createdTemplate ? 'Отменить' : 'Создать шаблон'"
        @click="
          createdTemplate = !createdTemplate;
          callNotification(createdTemplate);
        "
      />
    </div>
  </div>
</template>
<script lang="ts" setup>
import VueTemplateOrder from "../Forms/VueTemplateOrder.vue";
import VueYellowButton from "../Buttons/VueYellowButton.vue";
import VueCreateTemplate from "../Forms/VueCreateTemplate.vue";
import VueInfoBox from "../Info/VueInfoBox.vue";
import { computed, ref } from "vue";
import { Template } from "@/interfaces/Template";
import { userTemplatesStore } from "@/store/template";
import notification from "@/notifications";

let createdTemplate = ref(false);

const TEMPLATES = computed((): Template[] => {
  return userTemplatesStore().templates;
});

const callNotification = (state: boolean) => {
  if (!state) {
    notification.warning("Создание шаблона отменено!");
  }
};
</script>
<style lang="scss">
@import url(../../styles/variable.css);
@mixin warning {
  background-color: var(--error);
  padding: 15px;
  border-radius: 15px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  overflow: hidden;
  animation: showWarning 1s ease-out;
  & p {
    color: var(--white);
    text-align: end;
    text-wrap: nowrap;
    @media (max-width: 570px) {
      text-wrap: wrap;
      text-align: start;
    }
  }

  @keyframes showWarning {
    0% {
      width: 0px;
      opacity: 0;
    }
    90% {
      width: calc(100% + 10px);
    }
    100% {
      opacity: 1;
      width: calc(100%);
    }
  }
}
.not-templates {
  max-width: 600px;
  &__warning {
    @include warning();
    margin-bottom: 10px;
  }
}
.created__template {
  max-width: 600px;
  box-shadow: var(--shadow);
  padding: 20px;
  border-radius: 15px;
  color: var(--white);
}
</style>
