<template>
  <div class="performer-profile">
    <vue-user-info v-if="props.tabId === 1" :user-info="getUserInfo" />

    <div class="order-active__cards" v-if="props.tabId === 2">
      <vue-performer-active-card
        v-for="(card, index) of getUserInfo.activeOrder"
        :key="index"
        :card="card"
        v-if="getLengthActiveCards > 0"
        v-loading="loadingActiveCard"
      />
      <div class="order-active__warning" v-else-if="getLengthActiveCards === 0">
        <p class="order-active__warning--p">Активных заказов нет</p>
      </div>
    </div>

    <div class="order-done__cards" v-if="props.tabId === 3">
      <vue-performer-done-card
        v-for="(card, index) of getUserInfo?.doneOrder"
        :key="index"
        :card="card"
        v-if="getLengthDoneCards > 0"
        v-loading="loadingDoneCard"
      />
      <div class="order-done__warning" v-else-if="getLengthDoneCards === 0">
        <p class="order-done__warning--p">Завершенных заказов нет</p>
      </div>
    </div>

    <div class="user-settings" v-if="props.tabId === 4">
      <vue-performer-settings />
    </div>

    <div class="offer-profile" v-if="props.tabId === 5">
      <vue-offer-info-block />
    </div>

    <div class="statistics-block" v-if="props.tabId === 6">
      <vue-performer-statistics :user-info="userInfo" />
    </div>
  </div>
</template>
<script setup lang="ts">
import { computed, defineProps, ref } from "vue";
import performerInfo from "../../localDatabase/performerInfo.json";
import VueUserInfo from "../Forms/VueUserInfo.vue";
import PerformerInfo from "@/interfaces/PerformerInfo";
import VuePerformerDoneCard from "../Cards/VuePerformerDoneCard.vue";
import VuePerformerActiveCard from "../Cards/VuePerformerActiveCard.vue";
import VueOfferInfoBlock from "../Offer/VueOfferInfoBlock.vue";
import VuePerformerSettings from "../Settings/VuePerformerSettings.vue";
import VuePerformerStatistics from "../Statistics/VuePerformerStatistics.vue";
import { useUserStore } from "@/store/user";

const props = defineProps({
  tabId: Number,
});

const userStore = useUserStore();
const userInfo = userStore.userInfo;

let loadingDoneCard = ref(true);
let loadingActiveCard = ref(true);

//TODO(tolstov): будем смотреть на скороть запроса, скорее всего удалим
setTimeout(() => {
  loadingActiveCard.value = false;
}, 2000);
setTimeout(() => {
  loadingDoneCard.value = false;
}, 2000);

const getUserInfo = computed((): PerformerInfo => {
  return performerInfo;
});

const getLengthDoneCards = computed((): number => {
  if (getUserInfo) {
    return getUserInfo.value!.doneOrder.length;
  } else return 0;
});

const getLengthActiveCards = computed((): number => {
  if (getUserInfo) {
    return getUserInfo.value!.activeOrder.length;
  } else return 0;
});
</script>
<style lang="scss">
@mixin warning {
  background-color: var(--error);
  padding: 15px;
  border-radius: 15px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  overflow: hidden;
  animation: showWarning 1s ease-out;
  &--p {
    color: var(--white);
    text-align: end;
    text-wrap: nowrap;
  }
  @keyframes showWarning {
    0% {
      width: 0px;
      opacity: 0;
    }
    90% {
      width: calc(100% + 10px);
    }
    100% {
      opacity: 1;
      width: calc(100%);
    }
  }
}

.performer-profile {
  font-family: "sf-pro-400";
  font-size: 20px;
  width: 100%;
}

.order {
  &-done {
    &__warning {
      @include warning();
    }
  }
  &-active {
    &__warning {
      @include warning();
    }
  }
}
.statistics__block {
  display: flex;
  justify-content: center;
}
</style>
