<template>
  <vue-info-box> Статистика находится в разработке! </vue-info-box>
  <!-- TODO(tolstov): отключен блок на время разработки -->
  <div class="customer-statistics" v-if="false">
    <div class="customer-statistics__date block-white">
      <label>Дата регистрации</label>
      <p>{{ props.userInfo.startDate }}</p>
    </div>
    <div class="customer-statistics__filter">
      <vue-radio-group :items="radioGroupItems" v-model="selectedRadioItem" />
    </div>
    <div class="customer-statistics__info-order block-white">
      <p>
        Созданно заказов за <span>{{ getTitleOrder }}</span
        >:
      </p>
      <span>
        {{ getSumOrders }}
      </span>
    </div>

    <div class="customer-statistics__rating block-white">
      <p>Ваш рейтинг на бирже ({{ rating }}/5)</p>
      <vue-rating :number="rating" :muted="true" />
    </div>
  </div>
</template>
<script lang="ts" setup>
import { computed, defineProps, ref } from "vue";
import VueRadioGroup from "../RadioInputs/VueRadioGroup.vue";
import CustomerInfo from "@/interfaces/CustomerInfo";
import VueRating from "../Ratings/VueRating.vue";
import VueInfoBox from "../Info/VueInfoBox.vue";

const props = defineProps<{
  userInfo: CustomerInfo;
}>();

//TODO(tolstov): получаем из базы
const ratingSum = ref(6);

const getTitleOrder = computed((): string => {
  if (selectedRadioItem.value === 1) {
    return "все время";
  } else if (selectedRadioItem.value === 2) {
    return "этот месяц";
  } else if (selectedRadioItem.value === 3) {
    return "эту неделю";
  }
  return "";
});

const rating = ref(props.userInfo.rating);

//TODO(tolstov): будет приходить ответ от сервера
const getSumOrders = computed((): number => {
  if (selectedRadioItem.value === 1) {
    return 6;
  } else if (selectedRadioItem.value === 2) {
    return 5;
  } else if (selectedRadioItem.value === 3) {
    return 3;
  }
  return 0;
});

const selectedRadioItem = ref(1);

const radioGroupItems = [
  {
    id: 1,
    name: "все время",
    isActive: true,
  },
  {
    id: 2,
    name: "этот месяц",
    isActive: false,
  },
  {
    id: 3,
    name: "неделя",
    isActive: false,
  },
];
</script>
