<template>
  <div class="customer-setting">
    <vue-info-box>В процессе доработки...</vue-info-box>
    <!-- TODO(tolstov): отключены на время разработки уведомления в телеграмм -->
    <!-- <div class="customer-setting__telegram">
      <p>Уведомления в телеграмм</p>      
      <vue-switcher-ios v-model="isNotificationTelegram" />
    </div> -->

    <div class="customer-setting__title">
      <p>Настройка вкладок личного кабинета</p>
    </div>

    <div
      class="customer-setting__tabs"
      v-staggered-from="{
        delay: 0.05,
        selector: '.customer-setting__tab',
        yOffset: 20,
      }"
    >
      <div
        class="customer-setting__tab"
        v-for="(tab, index) of tabsLK"
        :key="index"
      >
        <p>{{ tab.title }}</p>
        <vue-switcher-ios
          v-model="tab.isActive"
          @change="updateTabLKActive(tab, tab.isActive)"
        />
      </div>
    </div>

    <div class="customer-setting__title h4">
      <p>Настройка вкладок меню</p>
    </div>

    <div
      class="customer-setting__tabs"
      v-staggered-from="{
        delay: 0.05,
        selector: '.customer-setting__tab',
        yOffset: 20,
      }"
    >
      <div
        class="customer-setting__tab"
        v-for="(tab, index) of tabsMenu"
        :key="index"
      >
        <p>{{ tab.title }}</p>
        <vue-switcher-ios
          v-model="tab.isActive"
          @change="updateTabMenuActive(tab, tab.isActive)"
        />
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { useSettingsStore } from "@/store/settings";
import VueSwitcherIos from "../Switchers/VueSwitcherIos.vue";
import notification from "@/notifications";
import VueInfoBox from "../Info/VueInfoBox.vue";
import { Tab } from "@/store/settings";

const store = useSettingsStore();

const tabsLK = computed(() => store.tabsLK);
const tabsMenu = computed(() => store.tabsMenu);

const isNotificationTelegram = computed({
  get: () => store.isNotificationTelegram,
  set: (value) => (store.isNotificationTelegram = value),
});

const showTabNotification = (tab: Tab, isActive: boolean) => {
  if (isActive) {
    notification.success(`Вкладка "${tab.title}" включена`);
  } else {
    notification.warning(`Вкладка "${tab.title}" выключена`);
  }
};

const updateTabLKActive = (tab: Tab, isActive: boolean) => {
  store.updateTabLKStatus(tab.id, isActive);
  showTabNotification(tab, isActive);
};

const updateTabMenuActive = (tab: Tab, isActive: boolean) => {
  store.updateTabMenuStatus(tab.id, isActive);
  showTabNotification(tab, isActive);
};
</script>

