<template>
  <div id="menuToggle" class="vue-burger" @click="onToggleMenu">
    <label class="toggle">
      <div class="circle">
        <div
          class="bar bar--top"
          :class="{ 'bar--top-active': isChecked }"
        ></div>
        <div
          class="bar bar--middle"
          :class="{ 'bar--middle-active': isChecked }"
        ></div>
        <div
          class="bar bar--bottom"
          :class="{ 'bar--bottom-active': isChecked }"
        ></div>
      </div>
    </label>
  </div>
</template>

<script setup lang="ts">
import { ref, watch } from "vue";

const props = defineProps<{
  modelValue: boolean;
}>();

const emit = defineEmits<{
  (event: "update:modelValue", value: boolean): void;
}>();

const isChecked = ref(props.modelValue);

watch(
  () => props.modelValue,
  (newVal) => {
    isChecked.value = newVal;
  }
);

const onToggleMenu = () => {
  isChecked.value = !isChecked.value;
  emit("update:modelValue", isChecked.value);
};
</script>

<style lang="scss" scoped>
@import url(../../styles/variable.css);

.vue-burger {
  box-shadow: var(--shadow);
  padding: 10px;
  border-radius: var(--radius);
  cursor: pointer;
}

#menuToggle {
  .toggle {
    position: relative;
    width: 40px;
    height: 40px;
    cursor: pointer;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .circle {
    position: relative;
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .bar {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 20px;
    height: 4px;
    border-radius: calc(4px / 2);
    background: var(--text-black);
    opacity: 1;
    transition: all 0.35s cubic-bezier(0.5, -0.35, 0.35, 1.5);
  }

  .bar--top {
    top: 8px;
    transition-property: top, transform;
    transition-delay: 0s, 0s;
  }

  .bar--middle {
    top: 50%;
    transform: translate(-50%, -50%);
    transition-property: opacity, transform;
    transition-duration: 0.35s, 0.35s;
    transition-delay: 0s, 0s;
  }

  .bar--bottom {
    bottom: 8px;
    transition-property: bottom, transform;
    transition-delay: 0s, 0s;
  }

  .bar--top-active {
    top: 50% !important;
    transform: translate(-50%, -50%) rotate(45deg) !important;
    transition-delay: 0s, 0.35s !important;
  }

  .bar--middle-active {
    opacity: 0 !important;
    transform: translate(-50%, -50%) scale(0) !important;
    transition-delay: 0s, 0.35s !important;
  }

  .bar--bottom-active {
    bottom: 50% !important;
    transform: translate(-50%, 50%) rotate(-45deg) !important;
    transition-delay: 0s, 0.35s !important;
  }
}
</style>
