<template>
  <div class="customer-statistics">
    <div class="customer-statistics__date block-white">
      <label>Дата регистрации</label>
      <p>{{ props.userInfo.startDate }}</p>
    </div>
    <div class="customer-statistics__filter">
      <vue-radio-group :items="radioGroupItems" v-model="selectedRadioItem" />
    </div>
    <div class="customer-statistics__info-order block-white">
      <p>
        Созданно заказов за <span>{{ getTitleOrder }}</span>
        :
      </p>
      <span>
        {{ getSumOrders }}
      </span>
    </div>

    <div class="customer-statistics__rating block-white">
      <p>Ваш рейтинг на бирже ({{ rating }}/5)</p>
      <vue-rating :number="rating" :muted="true" />
    </div>

    <div class="customer-statistics__rating-history block-white">
      <div class="customer-statistics__rating-history__info">
        <p>История вашего рейтинга</p>
        <span>(всего вас оценили {{ ratingSum }} раз)</span>
      </div>
      <div
        class="rating-item"
        v-for="(rating, index) of getRatingsHistory"
        :key="index"
      >
        <p class="rating-item__date">{{ rating.date }}</p>
        <vue-rating :number="rating.rating" :muted="true" />
      </div>
    </div>
  </div>
</template>
<script lang="ts" setup>
import { computed, defineProps, ref } from "vue";
import VueRadioGroup from "../RadioInputs/VueRadioGroup.vue";
import CustomerInfo from "@/interfaces/CustomerInfo";
import VueRating from "../Ratings/VueRating.vue";

const props = defineProps<{
  userInfo: CustomerInfo;
}>();

//TODO(tolstov): получаем из базы
const ratingSum = ref(6);

const getTitleOrder = computed((): string => {
  if (selectedRadioItem.value === 1) {
    return "все время";
  } else if (selectedRadioItem.value === 2) {
    return "этот месяц";
  } else if (selectedRadioItem.value === 3) {
    return "эту неделю";
  }
  return "";
});

const rating = ref(props.userInfo.rating);

//TODO(tolstov): будет приходить ответ от сервера
const getSumOrders = computed((): number => {
  if (selectedRadioItem.value === 1) {
    return 6;
  } else if (selectedRadioItem.value === 2) {
    return 5;
  } else if (selectedRadioItem.value === 3) {
    return 3;
  }
  return 0;
});

//TODO(tolstov): будет в запросе
const getRatingsHistory = computed(() => {
  if (selectedRadioItem.value === 1) {
    return [
      {
        id: 1,
        rating: 4,
        date: "15.02.2025",
      },
      {
        id: 2,
        rating: 3,
        date: "17.02.2025",
      },
      {
        id: 3,
        rating: 2,
        date: "20.02.2025",
      },
      {
        id: 4,
        rating: 3,
        date: "03.03.2025",
      },
      {
        id: 5,
        rating: 1,
        date: "06.03.2025",
      },
      {
        id: 6,
        rating: 5,
        date: "14.03.2025",
      },
    ];
  } else if (selectedRadioItem.value === 2) {
    return [
      {
        id: 1,
        rating: 3,
        date: "03.03.2025",
      },
      {
        id: 2,
        rating: 1,
        date: "06.03.2025",
      },
      {
        id: 3,
        rating: 5,
        date: "14.03.2025",
      },
    ];
  } else if (selectedRadioItem.value === 3) {
    return [
      {
        id: 1,
        rating: 5,
        date: "14.03.2025",
      },
    ];
  }
  return null;
});

const selectedRadioItem = ref(1);

const radioGroupItems = [
  {
    id: 1,
    name: "За все время",
    isActive: true,
  },
  {
    id: 2,
    name: "За этот месяц",
    isActive: false,
  },
  {
    id: 3,
    name: "За неделю",
    isActive: false,
  },
];
</script>
