<template>
  <div class="user-profile" v-if="!isEditInfo">
    <div class="user__status shadow-block">
      Ваш статус: {{ props.userRole.textRole }}
      <img src="../../assets/icons/user.svg" alt="" />
    </div>
    <div class="user__lastName user-info-block">
      <label>Фамилия</label>
      <p>{{ store.user.lastName }}</p>
    </div>
    <div class="user__name user-info-block">
      <label>Имя</label>
      <p>{{ store.user.name }}</p>
    </div>
    <div class="user__serName user-info-block">
      <label>Отчество</label>
      <p>{{ store.user.serName }}</p>
    </div>
    <div class="user__phone user-info-block">
      <label>Номер телефона</label>
      <vue-number-input
        v-model="userEditInfo.userPhone.phone.value"
        v-model:is-valid="userEditInfo.userPhone.isValid.value"
        :placeholder="'+7 (###) ###-##-##'"
        :type="'phone'"
        :disabled="true"
      />
    </div>
    <div class="user__edit-info">
      <vue-button
        :title="'Изменить'"
        @click="isEditInfo = !isEditInfo"
        :color="'yellow'"
      />
    </div>
  </div>

  <div class="user-profile" v-else>
    <div class="user__lastName user-info-block">
      <label>Фамилия</label>
      <vue-text-input
        v-model="userEditInfo.userLastName.lastName.value"
        v-model:is-valid="userEditInfo.userLastName.isValid.value"
        :placeholder="'Фамилия'"
      />
    </div>
    <div class="user__name user-info-block">
      <label>Имя</label>
      <vue-text-input
        v-model="userEditInfo.userName.name.value"
        v-model:is-valid="userEditInfo.userName.isValid.value"
        :placeholder="'Имя'"
      />
    </div>
    <div class="user__serName user-info-block">
      <label>Отчество</label>
      <vue-text-input
        v-model="userEditInfo.userSerName.serName.value"
        v-model:is-valid="userEditInfo.userSerName.isValid.value"
        :placeholder="'Отчество'"
      />
    </div>
    <div class="user__phone user-info-block">
      <label>Номер телефона</label>
      <vue-number-input
        v-model="userEditInfo.userPhone.phone.value"
        v-model:is-valid="userEditInfo.userPhone.isValid.value"
        :placeholder="'+7 (###) ###-##-##'"
        :type="'phone'"
        :disabled="false"
      />
    </div>
    <div class="user__edit-info" v-loading="isLoadingUpdateInfoUser">
      <!-- <vue-yellow-button :title="'Сохранить'" @click="onUpdateInfoUser" /> -->
      <vue-button
        :title="'Сохранить'"
        @click="onUpdateInfoUser"
        :color="'yellow'"
      />
    </div>
  </div>
</template>

<script lang="ts" setup>
import { defineProps, ref } from "vue";
import PerformerInfo from "@/interfaces/PerformerInfo";
import VueYellowButton from "../Buttons/VueYellowButton.vue";
import VueButton from "../Buttons/VueButton.vue";
import VueNumberInput from "../Inputs/VueNumberInput.vue";
import VueTextInput from "../Inputs/VueTextInput.vue";
import { useUserProfileStore } from "@/store/profile";
import { EditUser } from "@/store/profile";
import CustomerInfo from "@/interfaces/CustomerInfo";
import notification from "@/notifications";
import { UserRole } from "@/types/UserRoles";

const props = defineProps({
  userInfo: {
    type: Object as () => PerformerInfo | CustomerInfo,
    required: true,
  },
  userRole: {
    type: Object as () => UserRole,
    required: true,
  },
});

const store = useUserProfileStore();
const isEditInfo = ref(false);
const isLoadingUpdateInfoUser = ref(false);

const userEditInfo = {
  userName: {
    name: ref(store.user.name),
    isValid: ref(false),
  },
  userLastName: {
    lastName: ref(store.user.lastName),
    isValid: ref(false),
  },
  userSerName: {
    serName: ref(store.user.serName),
    isValid: ref(false),
  },
  userPhone: {
    phone: ref(store.user.phone),
    isValid: ref(false),
  },
};

//TODO(tolstov): будущий запрос
const onUpdateInfoUser = () => {
  isLoadingUpdateInfoUser.value = true;
  setTimeout(() => {
    const user: EditUser = {
      userName: userEditInfo.userName.name.value,
      userLastName: userEditInfo.userLastName.lastName.value,
      userSerName: userEditInfo.userSerName.serName.value,
      userPhone: userEditInfo.userPhone.phone.value,
    };

    store.updateUserInfo(user);
    isEditInfo.value = !isEditInfo.value;

    isLoadingUpdateInfoUser.value = false;
    notification.success("Ваша информация обновлена!");
  }, 1500);
};
</script>

