<template>
  <div class="rating" :class="{ muted: muted }">
    <input
      v-for="star in 5"
      :key="star"
      type="radio"
      :id="`star${star}`"
      :value="star"
      v-model="selectedRating"
      :disabled="muted"
    />
    <label
      v-for="star in 5"
      :key="star"
      :for="`star${star}`"
      :title="getTitle(star)"
      @mouseover="hoverRating = star"
      @mouseleave="hoverRating = null"
      :class="{
        active: isStarActive(star),
        hovered: isStarHovered(star),
      }"
      :style="{
        '--star-color': getRatingColor(selectedRating),
        '--hover-color': getRatingColor(hoverRating || selectedRating),
      }"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        height="1em"
        viewBox="0 0 576 512"
      >
        <path
          d="M316.9 18C311.6 7 300.4 0 288.1 0s-23.4 7-28.8 18L195 150.3 51.4 171.5c-12 1.8-22 10.2-25.7 21.7s-.7 24.2 7.9 32.7L137.8 329 113.2 474.7c-2 12 3 24.2 12.9 31.3s23 8 33.8 2.3l128.3-68.5 128.3 68.5c10.8 5.7 23.9 4.9 33.8-2.3s14.9-19.3 12.9-31.3L438.5 329 542.7 225.9c8.6-8.5 11.7-21.2 7.9-32.7s-13.7-19.9-25.7-21.7L381.2 150.3 316.9 18z"
        ></path>
      </svg>
    </label>
  </div>
</template>

<script lang="ts" setup>
import { defineProps, defineEmits, ref, watch } from "vue";

const props = defineProps<{
  modelValue?: number;
  number?: number;
  muted?: boolean;
}>();

const emit = defineEmits(["update:modelValue"]);

const selectedRating = ref(props.modelValue || Math.round(props.number || 0));

const hoverRating = ref<number | null>(null);

watch(selectedRating, (newValue) => {
  emit("update:modelValue", newValue);
});

watch(
  () => props.modelValue,
  (newValue) => {
    if (newValue !== undefined) {
      selectedRating.value = newValue;
    }
  }
);

watch(
  () => props.number,
  (newValue) => {
    if (newValue !== undefined) {
      selectedRating.value = Math.round(newValue);
    }
  }
);

const getTitle = (star: number) => {
  const titles = ["Bad", "Okay", "Good", "Great!", "Excellent!"];
  return titles[star - 1];
};

const isStarActive = (star: number) => {
  return star <= selectedRating.value;
};

const isStarHovered = (star: number) => {
  return hoverRating.value !== null && star <= hoverRating.value;
};

const getRatingColor = (rating: number) => {
  //порядок имеет значение
  const colors = ["#ef4444", "#feb084", "#eab308", "#c3d068", "#19c37d"];
  return colors[rating - 1] || "#666";
};
</script>

<style lang="scss" scoped>
@import url(../../styles/variable.css);
.rating {
  display: inline-flex;
  flex-direction: row;

  & > input {
    display: none;
  }

  & > label {
    cursor: pointer;
    font-size: 30px;
    margin-right: 4px;
  }

  & > label > svg {
    fill: rgb(185 185 185);
    transition: fill 0.3s ease;
  }

  & > label.active > svg {
    fill: var(--star-color);
  }

  & > label.hovered > svg {
    fill: var(--hover-color);
  }

  &.muted {
    pointer-events: none;

    & > label > svg {
      fill: rgb(185 185 185);
    }

    & > label.active > svg {
      fill: var(--star-color);
    }
  }
}
</style>
